import {postRequest} from "./api";

export const initMenu = (router,store) => {
    if (store.state.routes.length>0){
        return
    }
    postRequest('oasys-back-end/initialMenu', {username:localStorage.getItem("username")}).then(data=>{
        if (data){
            let fmtRoutes = formatRoutes(data.data);
            router.addRoutes(fmtRoutes);
            store.commit('initRoutes',fmtRoutes)
        }
    })
}

export const formatRoutes = (routes) => {
    let fmtRoutes = [];
    routes.forEach(router=>{
        let{
            path,
            component,
            name,
            menuIcon,
            children
        }=router;
        if (children && children instanceof Array){
            children = formatRoutes(children);
        }
        let fmRouter = {
            path:path,
            name:name,
            meta: {title:name},
            menuIcon: menuIcon,
            children: children,
            component(resolve){
                if (component.startsWith('info')){
                    require(['../views/userInfo/'+component+'.vue'],resolve)
                }else if (component.startsWith('resi')){
                    require(['../views/resign/'+component+'.vue'],resolve)
                }else if (component.startsWith('vacation')){
                    require(['../views/vacation/'+component+'.vue'],resolve)
                }else if(component.startsWith('checkOn')){
                    require(['../views/checkOn/'+component+'.vue'],resolve)
                }else if (component.startsWith('incr')){
                    require(['../views/increasingMoney/'+component+'.vue'],resolve)
                }else if (component.startsWith('performance')){
                    require(['../views/performance/'+component+'.vue'],resolve)
                }
                else {
                    require(['../views/'+component+'.vue'],resolve)
                }

            }
        }
        fmtRoutes.push(fmRouter)
    })
    return fmtRoutes
}





