import Vue from 'vue'
import Vuex from 'vuex'
import {Notification} from 'element-ui';
import {getRequest} from "../util/api";
// import '../utils/stomp'
// import '../utils/sockjs'

Vue.use(Vuex)


const store = new Vuex.Store({
    state: {
        routes: [],
        user: [],
    },
    mutations: {
        initRoutes(state, menus) {
            state.routes = menus
        },
        initCurrentUser(state,user){
            state.user = user
        }
    }
})

export default store;



